import Button from "react-bootstrap/Button";
import axios from "axios";
import Form from "react-bootstrap/Form";
import {useState} from "react";

const ExecutionRow = (props) => {

    const [side, setSide] = useState('BUY');

    const sideHandler = (event) => {
        setSide(event.target.value);
    };

    const updater = (val) => {
        props.update(val)
    };

    const executeRobot = (event) => {
        const robotStatus = event.target.value
        if (robotStatus === "inactive"){
            axios.post(props.server + 'new_task/', {
            process: "robots.processes.run_robot.run_robot",
            task_name: props.robot + "_robot_execution",
            arguments: [props.robot, side],
        })
            .then(function (response) {
                // if (response['data'] == 'New Portfolio was created!') {
                //     // window.location.reload();
                // } else {
                //     alert(response['data']);
                // }
            })
            .catch((error) => {
                console.error('Error Message:', error);
            });
        } else {
            axios.post(props.server + 'robots/update_robot/', {
            'name': props.robot,
            'status': 'inactive'
        })
            .then(function (response) {
                // if (response['data'] == 'New Portfolio was created!') {
                //     // window.location.reload();
                // } else {
                //     alert(response['data']);
                // }
                console.log(response)
                alert(response['data']['response']);
            })
            .catch((error) => {
                console.error('Error Message:', error);
            });
        };
        updater(event.target.value);

    };

    let buttonTxt = ''
    if (props.status === 'inactive'){
        buttonTxt = 'Run'
    } else {
        buttonTxt = 'Stop'
    };

    return (
        <tr>
            <td>
                {props.robot}
            </td>
            <td>
                {props.timeframe}
            </td>
            <td>
                {props.status}
            </td>
            <td>
                OPTION
            </td>
            <td>
               <Form.Control onChange={sideHandler} defaultValue={side} as="select">
                    <option value={'BUY'}>Buy</option>
                    <option value={'SELL'}>Sell</option>
                </Form.Control>
            </td>
            <td>
                <Button onClick={executeRobot} value={props.status}>{buttonTxt}</Button>
            </td>
        </tr>
    );

};

export default ExecutionRow;